.header {
  width: 100%;
  border-bottom: 1px solid $lipstick;
  padding: 30px 0;
  background: $black;
  &__block {
    display: flex;
    &__logo {
      h2 {
        font-family: 'Informal011 BT';
        color: $lipstick-two;
        font-size: 37.5px;
        font-weight: 900;
        line-height: 0.98;
        letter-spacing: 0.9px;
        position: relative;
        display: inline-block;
        span {
          color: $white; }
        p {
          font-size: 9.6px;
          font-weight: 500;
          color: $pinkish-grey-three;
          letter-spacing: 0.2px;
          position: absolute;
          right: 5px; } } }
    &__phone {
      display: flex;
      flex-direction: column;
      position: relative;
      padding: 0 45px;
      padding-right: 25px;
      border-right: 1px solid $lipstick;
      i {
        position: absolute;
        color: $lipstick-two;
        font-size: 26px;
        left: -5px;
        top: 7px; }
      a {
        font-weight: normal;
        color: $gunmetal; }
      a:nth-child(2) {
        font-size: 16px;
        font-weight: 500;
        color: $silver;
        padding-bottom: 5px; } }
    &__location {
      display: flex;
      flex-direction: column;
      position: relative;
      padding-left: 70px;
      i {
        position: absolute;
        color: $lipstick-two;
        font-size: 28px;
        left: 30px;
        top: 7px; }
      a {
        font-size: 16px;
        color: $cool-grey;
        p {
          color: $gunmetal;
          padding-top: 5px; } } }
    &__btn {
      a {
        padding: 15px 17px;
        background: $lipstick;
        display: inline-block;
        color: $white;
        font-size: 16px;
        line-height: 1.07;
        i {
          padding-right: 10px; } } } } }
.header-toolbar {
  nav {
    position: absolute; }
  a {
    color: $white;
    font-size: 16px;
    font-weight: 500;
    padding: 20px 0;
    padding-right: 50px;
    display: inline-block;
    text-transform: uppercase; } }
